"use client";

import React, { useEffect, useMemo, useState } from "react";
import Link from "next/link";
import {
  ArrowPathIcon,
  DocumentTextIcon,
  MagnifyingGlassIcon,
  FunnelIcon,
  ClockIcon,
  ExclamationTriangleIcon,
} from "@heroicons/react/24/outline";

type ComparisonItem = {
  id: number;
  document_name: string;
  version_old_label: string;
  version_new_label: string;
  created_at: string;
  overall_risk_level?: "LOW" | "MEDIUM" | "HIGH" | string | null;
  changes_count?: number;
};

type Risk = "HIGH" | "MEDIUM" | "LOW";

const API_BASE =
  process.env.NEXT_PUBLIC_API_BASE?.trim() || "http://127.0.0.1:8000";

/* ---------------- helpers ---------------- */

function normalizeRisk(r?: string | null): Risk {
  const x = (r || "").toUpperCase();
  if (x.includes("HIGH")) return "HIGH";
  if (x.includes("MEDIUM")) return "MEDIUM";
  return "LOW";
}

function formatDate(iso: string) {
  try {
    return new Date(iso).toLocaleString("th-TH", {
      month: "short",
      day: "numeric",
      year: "numeric",
      hour: "2-digit",
      minute: "2-digit",
    });
  } catch {
    return iso;
  }
}

function RiskPill({ r }: { r: Risk }) {
  const base =
    "inline-flex items-center rounded-full px-2.5 py-1 text-[11px] font-extrabold border";
  if (r === "HIGH")
    return (
      <span className={`${base} bg-red-50 text-red-700 border-red-200`}>
        HIGH
      </span>
    );
  if (r === "MEDIUM")
    return (
      <span className={`${base} bg-amber-50 text-amber-700 border-amber-200`}>
        MEDIUM
      </span>
    );
  return (
    <span className={`${base} bg-emerald-50 text-emerald-700 border-emerald-200`}>
      LOW
    </span>
  );
}

function clamp(n: number, min: number, max: number) {
  return Math.max(min, Math.min(max, n));
}

/* ---------------- page ---------------- */

export default function DocumentsPage() {
  const [items, setItems] = useState<ComparisonItem[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const [q, setQ] = useState("");
  const [riskFilter, setRiskFilter] = useState<"ALL" | Risk>("ALL");
  const [sort, setSort] = useState<
    "UPDATED_DESC" | "NAME_ASC" | "RISK_DESC"
  >("UPDATED_DESC");

  const fetchComparisons = async () => {
    setLoading(true);
    setError(null);
    try {
      const res = await fetch(`${API_BASE}/comparisons?limit=250`, {
        cache: "no-store",
      });
      if (!res.ok)
        throw new Error(`โหลด comparisons ไม่สำเร็จ (${res.status})`);
      const data: ComparisonItem[] = await res.json();
      setItems(Array.isArray(data) ? data : []);
    } catch (e: any) {
      setError(e?.message || "เกิดข้อผิดพลาดในการโหลดข้อมูล");
      setItems([]);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchComparisons();
  }, []);

  const computed = useMemo(() => {
    const list = [...items].sort(
      (a, b) =>
        new Date(b.created_at).getTime() -
        new Date(a.created_at).getTime()
    );

    type DocRow = {
      doc: string;
      comparisons: number;
      versionsEstimated: number;
      totalChanges: number;
      lastUpdatedISO: string;
      lastComparisonId: number | null;
      lastRisk: Risk;
      lastFromTo: string;
    };

    const byDoc = new Map<
      string,
      {
        doc: string;
        comparisons: number;
        vset: Set<string>;
        totalChanges: number;
        lastUpdatedISO: string;
        lastComparisonId: number | null;
        lastRisk: Risk;
        lastFromTo: string;
      }
    >();

    for (const x of list) {
      const doc = (x.document_name || "Untitled").trim() || "Untitled";
      const cur =
        byDoc.get(doc) ||
        {
          doc,
          comparisons: 0,
          vset: new Set<string>(),
          totalChanges: 0,
          lastUpdatedISO: x.created_at,
          lastComparisonId: x.id,
          lastRisk: normalizeRisk(x.overall_risk_level),
          lastFromTo: `${x.version_old_label} → ${x.version_new_label}`,
        };

      cur.comparisons += 1;
      if (x.version_old_label) cur.vset.add(x.version_old_label);
      if (x.version_new_label) cur.vset.add(x.version_new_label);
      cur.totalChanges += x.changes_count ?? 0;

      if (!byDoc.has(doc)) {
        cur.lastUpdatedISO = x.created_at;
        cur.lastComparisonId = x.id;
        cur.lastRisk = normalizeRisk(x.overall_risk_level);
        cur.lastFromTo = `${x.version_old_label} → ${x.version_new_label}`;
      }

      byDoc.set(doc, cur);
    }

    const rows: DocRow[] = [...byDoc.values()].map((x) => ({
      doc: x.doc,
      comparisons: x.comparisons,
      versionsEstimated: x.vset.size,
      totalChanges: x.totalChanges,
      lastUpdatedISO: x.lastUpdatedISO,
      lastComparisonId: x.lastComparisonId,
      lastRisk: x.lastRisk,
      lastFromTo: x.lastFromTo,
    }));

    const qq = q.trim().toLowerCase();
    let filtered = rows.filter((r) => {
      const okQ = !qq || r.doc.toLowerCase().includes(qq);
      const okRisk = riskFilter === "ALL" || r.lastRisk === riskFilter;
      return okQ && okRisk;
    });

    const riskScore = (r: Risk) =>
      r === "HIGH" ? 3 : r === "MEDIUM" ? 2 : 1;

    filtered.sort((a, b) => {
      if (sort === "NAME_ASC") return a.doc.localeCompare(b.doc);
      if (sort === "RISK_DESC") {
        const d = riskScore(b.lastRisk) - riskScore(a.lastRisk);
        if (d !== 0) return d;
      }
      return (
        new Date(b.lastUpdatedISO).getTime() -
        new Date(a.lastUpdatedISO).getTime()
      );
    });

    const topChanged = [...rows]
      .sort((a, b) => b.totalChanges - a.totalChanges)
      .slice(0, 5);

    const maxChanges = Math.max(
      1,
      ...topChanged.map((x) => x.totalChanges)
    );

    return { filtered, topChanged, maxChanges };
  }, [items, q, riskFilter, sort]);

  /* ---------------- UI ---------------- */

  return (
    <div className="max-w-7xl mx-auto space-y-6">
      {/* Header */}
      <div className="rounded-3xl border border-slate-200 bg-white p-6 shadow-sm">
        <div className="flex items-start justify-between gap-4">
          <div>
            <h1 className="text-xl font-extrabold text-slate-900">
              Documents
            </h1>
            <p className="mt-1 text-sm font-semibold text-slate-600">
              ภาพรวมเอกสารจากผลการเปรียบเทียบล่าสุด
            </p>
          </div>

          <div className="flex items-center gap-2">
            <button
              onClick={fetchComparisons}
              disabled={loading}
              className="inline-flex items-center gap-2 rounded-full bg-slate-900 px-4 py-2 text-sm font-extrabold text-white"
            >
              <ArrowPathIcon
                className={`h-4 w-4 ${loading ? "animate-spin" : ""}`}
              />
              Refresh
            </button>

            <Link
              href="/history"
              className="inline-flex items-center gap-2 rounded-full border border-slate-200 bg-white px-4 py-2 text-sm font-extrabold"
            >
              <ClockIcon className="h-5 w-5" />
              History
            </Link>
          </div>
        </div>

        {error && (
          <div className="mt-4 rounded-2xl border border-red-200 bg-red-50 p-4 text-sm font-semibold text-red-800 flex gap-2">
            <ExclamationTriangleIcon className="h-5 w-5" />
            {error}
          </div>
        )}
      </div>

      {/* Documents list */}
      <div className="rounded-3xl border border-slate-200 bg-white shadow-sm">
        <div className="divide-y divide-slate-200">
          {computed.filtered.map((r) => (
            <div key={r.doc} className="p-4 flex justify-between">
              <div>
                <div className="flex items-center gap-2">
                  <div className="font-extrabold">{r.doc}</div>
                  <RiskPill r={r.lastRisk} />
                </div>
                <div className="mt-1 text-xs font-semibold text-slate-600">
                  {r.comparisons} comparisons • {r.versionsEstimated} versions
                  • {r.totalChanges} changes • Updated{" "}
                  {formatDate(r.lastUpdatedISO)}
                </div>
              </div>

              {r.lastComparisonId && (
                <Link
                  href={`/compare/${r.lastComparisonId}`}
                  className="rounded-xl bg-slate-900 text-white px-4 py-2 text-sm font-extrabold"
                >
                  Open latest
                </Link>
              )}
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}
